<template>
  <div class="home">
    <List :list-data="listData" :data-name="'home'"></List>
  </div>
</template>
<script>
import List from '../components/List'
export default {
  data(){
    return {
      listData:[],
      banner:[]
    }
  },
  components:{
    List
  },
  mounted(){
    axios({
      url:'/api/home',
      params:{_page:1,_limit:10}
    }).then(
      res=>this.listData=res.data.data
    )
  }
}
</script>