<template>
  <div id="app" class="box">
    <Search v-show="$root.$data.bSearch"></Search>
    <Header v-show="$root.$data.bNav"></Header>
    <router-view></router-view>
    <Footer v-show="$root.bFoot"></Footer>
  </div>
</template>

<script>
import Search from './components/Search'
import Header from './components/Header'
import Footer from './components/Footer'

export default {
  name: 'app',
  components: {
    Search,Header,Footer
  },
  methods:{
    checkPath(path){//路由检测
      console.log('path',path);
      if(/home|follow|column/.test(path)){
        this.$root.$data.bNav=true;
        this.$root.$data.bFoot=true;
        this.$root.$data.bSearch=true;
      }
      if(/user|customer|store|carts|login|reg|Detail|User/.test(path)){
        this.$root.$data.bSearch=false;
        this.$root.$data.bNav=false;
        this.$root.$data.bFoot=true;
      }
      if(/Detail/.test(path)){
        this.$root.$data.bSearch=false;
        this.$root.$data.bNav=false;
        this.$root.$data.bFoot=false;
      }
    }
  },
  watch:{
    $route:{
      handler(current,prev){
        // current.path==/home|/follow|/colukn|/detail/23423234
        this.checkPath(current.path)
      },
      immediate:true,
      deep:true
    }
  }
}
</script>

