import Vue from 'vue'
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import Home from '../pages/Home.vue'
import Follow from '../pages/Follow.vue'
import AllProduct from '../pages/AllProduct.vue'
import New_shelves from '../pages/New_shelves.vue'
import Category_all from '../pages/Category_all.vue'
import Bulletin from '../pages/Bulletin.vue'
import Column from '../pages/Column.vue'
import User from '../pages/User.vue'
import Login from '../pages/Login.vue'
import Reg from '../pages/Reg.vue'
import Detail from '../pages/Detail.vue'
import Error from '../pages/Error.vue'
import Customer from '../pages/Customer.vue'
import Store from '../pages/Store.vue'
import Carts from '../pages/Carts.vue'
import Member from '../pages/Member.vue'
import Search from '../pages/Search.vue'

let routes=[
  {path:'/home',component:Home},
  {path:'/Follow',component:Follow},
  {path:'/AllProduct',component:AllProduct},
  {path:'/New_shelves',component:New_shelves},
  {path:'/Category_all',component:Category_all},
  {path:'/Bulletin',component:Bulletin},
  {path:'/Customer',component:Customer},
  {path:'/Store',component:Store},
  {path:'/Carts',component:Carts},
  {path:'/Member',component:Member},
  {path:'/Column',component:Column},
  {path:'/User',component:User},
  {path:'/Login',component:Login},
  {path:'/Reg',component:Reg},
  {path:'/Detail/:id',component:Detail,name:'Detail'},
  {path:'/Search',component:Search},
  {path:'/',redirect:'/home'},
  {path:'*',component:Error},

];

export default new VueRouter({
  routes,
  mode: 'history'
})